<style>
  .order-id {
    cursor: pointer;
    color: #2077FF;
  }
  .order-id:hover {
    color: #409EFF;
  }

  .group-order-detail-wraper {
    background-color: #FFF;
    padding: 20px 10px;
  }
  .detail-mesg-item-wraper {
    padding: 20px 0;
    border-bottom: 1px solid #ccc;
  }
  .detail-mesg-item-wraper:last-child {
    border-bottom: 0;
  }
  .first-detail-mesg-item {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: space-between;
  }

  /*详情第一条信息*/
  .group-no-msg {
    font-size: 16px;
    color: #222222;
    font-weight: bold;
    text-align: left;
    padding: 0 6px;
  }
  .o-status {
    padding: 0 6px;
    width: auto;
    font-size: 16px;
    color: #3080fe;
    font-weight: bold;
    text-align: right;
  }
  
  /*详情第二条信息*/
  .second-detail-mesg-item, .third-detail-mesg-item, .fourth-detail-mesg-item {
    padding: 0 6px;
    margin-top: 10px;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
  }
  .g-o-limit-p-c {
    margin-left: 50px;
    font-weight: bold;
    color: #222;
  }
  .has-p-c {
    margin-left: 30px;
    font-weight: bold;
    color: #222;
  }

  .ml50 {
    margin-left: 50px;
  }
  .color-2-bold {
    color: #222;
    font-weight: bold;
  }
</style>

<template>
  <div class="group-order-detail-wraper">
    <!-- 第一条信息 -->
    <div class="detail-mesg-item-wraper">
      <div class="first-detail-mesg-item">
        <p class="group-no-msg">
          拼团号&nbsp;&nbsp;{{groupInfo.groupNo}}
        </p>
        <p class="o-status">
          {{groupInfo.groupStatusStr}}
        </p>
      </div>
    </div>
    
    <!-- 第二条信息 -->
    <div class="detail-mesg-item-wraper">
      <p class="group-no-msg">
        拼团商品
      </p>
      <div class="second-detail-mesg-item">
        <p class="goods-msg">
          {{groupInfo.goodsNo}} - {{groupInfo.platCityActivityGoodsName}} &nbsp;&nbsp;约{{groupInfo.goodsSpec}}/份
        </p>
        <p class="g-o-limit-p-c">
          {{groupInfo.joinLimitNum}} 人团
        </p>
        <p class="has-p-c">
          已成团 {{groupInfo.groupJoinedNum}}人
        </p>
      </div>
    </div>

    <!-- 第三条信息 -->
    <div class="detail-mesg-item-wraper">
      <p class="group-no-msg">
        成团时间
      </p>
      <div class="third-detail-mesg-item">
        <p>
          <span>成团时长：</span><span class="color-2-bold">{{groupInfo.lastFor}} 分钟</span>
        </p>
        <p class="ml50">
          <span>开团时间：</span><span class="color-2-bold">{{groupInfo.createTime}}</span>
        </p>
        <p class="ml50">
          <span>成团时间：</span><span class="color-2-bold">{{groupInfo.successTime}}</span>
        </p>
      </div>
    </div>
    
    <!-- 第四条信息 -->
    <div class="detail-mesg-item-wraper">
      <p class="group-no-msg">
        成团订单
      </p>
      <div class="fourth-detail-mesg-item">
        <el-table
          class="fixed-height"
          empty-text="没有查询到您想要的数据"
          :data="groupOrderList"
          style="width:100%;"
          size="small">
          <el-table-column min-width="178px" prop="orderNo" label="订单号">
            <template slot-scope="scope" size="small">
              <div class="order-id" v-if="groupInfo.groupStatus === 20">
                <router-link :to="scope.row.redirectUrl" target="_blank">{{scope.row.orderNo}}</router-link>
              </div>
              <div v-else>{{scope.row.orderNo}}</div>
            </template>
          </el-table-column>
          <el-table-column min-width="86px" prop="orderType" label="配送类型" :formatter="orderTypeFormater"></el-table-column>
          <el-table-column min-width="130px" prop="storeName" label="所属门店"></el-table-column>
          <el-table-column min-width="120px" prop="receiverName" label="收货人" :formatter="emptyValFormater"></el-table-column>
          <el-table-column min-width="120px" prop="receiverPhone" label="收货手机" :formatter="emptyValFormater"></el-table-column>
          <el-table-column min-width="86px" prop="orderPayAmount" label="订单实付"></el-table-column>
          <el-table-column min-width="86px" prop="orderStatusDesc" label="订单状态"></el-table-column>
          <el-table-column min-width="156px" prop="createTime" label="下单时间"></el-table-column>
          <el-table-column min-width="156px" prop="preDeliveryTime" label="要求送达/自提时间" :formatter="emptyValFormater"></el-table-column>
        </el-table>
      </div>
    </div>

  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  const requestUrl = {
    // 拼团订单-详情-拼团信息
    groupInfo: '/member/zt/group/groupInfo',
    // 拼团订单-详情-成团订单
    groupOrderList: '/member/zt/group/groupOrderList'
  }
  export default {
    data() {
      return {
        groupNo: '',
        groupInfo: {
          goodsNo: '',
          skuNo: '',
          goodsSpec: '',
          createTime: '', // 开团时间
          groupJoinedNum: '', // 成团人数
          groupNo: '', // 拼团号
          groupStatus: '', // 拼团状态
          groupStatusStr: '', // 拼团状态中文
          joinLimitNum: '', // 几人团
          lastFor: '', // 成团时长
          platCityActivityGoodsName: '', // 拼团商品
          successTime: '' // 成团时间
        },
        groupOrderList: []
      }
    },
    created() {
      this.groupNo = this.$route.params.groupNo
    },
    mounted() {
      if(this.groupNo) {
        // 获取详情
        this.getOrderDetail()
        // 拼团订单-详情-成团订单
        this.getGroupOrderList()
      }

    },
    methods: {
      /**
       * [getOrderDetail 获取订单详细信息]
       * @return {[type]} [description]
       */
      getOrderDetail() {
        // 全局展示loading
        utils.showLoading()
        this.$http.post(requestUrl.groupInfo, {source: 'web', groupNo: this.groupNo, DIFFCITY: true}).then(res => {
          // 清除全局loading
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              let groupInfoMesg = res.data
              this.groupInfo.goodsNo = groupInfoMesg.goodsNo || '--'
              this.groupInfo.skuNo = groupInfoMesg.skuNo || '--'
              this.groupInfo.goodsSpec = groupInfoMesg.goodsSpec || '--'
              this.groupInfo.createTime = groupInfoMesg.createTime || '--'
              this.groupInfo.groupJoinedNum = groupInfoMesg.groupJoinedNum || '0'
              this.groupInfo.groupNo = groupInfoMesg.groupNo || '--'
               // 拼团状态 10-待成团；20-已成团；30-拼团失败
              this.groupInfo.groupStatus = groupInfoMesg.groupStatus || '--'
              switch(groupInfoMesg.groupStatus) {
                case 10:
                  this.groupInfo.groupStatusStr = '待成团'
                break;
                case 20:
                  this.groupInfo.groupStatusStr = '成团成功'
                break;
                case 30:
                  this.groupInfo.groupStatusStr = '成团失败'
                break;
                case 40:
                  this.groupInfo.groupStatusStr = '已关闭'
                break;
                default:
                  this.groupInfo.groupStatusStr = '--'
                break;
              }
              this.groupInfo.joinLimitNum = groupInfoMesg.joinLimitNum || '0'
              this.groupInfo.lastFor = groupInfoMesg.lastFor || '--'
              this.groupInfo.platCityActivityGoodsName = groupInfoMesg.platCityActivityGoodsName || '--'
              this.groupInfo.successTime = groupInfoMesg.successTime || '--'
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          // 清除全局loading
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [getGroupOrderList 拼团订单-详情-成团订单]
       * @return {[type]} [description]
       */
      getGroupOrderList() {
        // 全局展示loading
        utils.showLoading()
        this.$http.post(requestUrl.groupOrderList, {source: 'web', groupNo: this.groupNo, DIFFCITY: true}).then(res => {
          // 清除全局loading
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data && res.data.list && res.data.list.length) {
              for(let item of res.data.list) {
                item.redirectUrl = `/order_mgt/detail/${item.orderNo}`
              }
              this.groupOrderList = res.data.list
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          // 清除全局loading
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [orderTypeFormater 格式化]
       * @param  {[type]} row       [description]
       * @param  {[type]} column    [description]
       * @param  {[type]} cellValue [description]
       * @return {[type]}           [description]
       */
      orderTypeFormater(row, column, cellValue) {
        // 1-及时送；2-预约送；3-自提单  5-快递配送
        let cellValueStr = ''
        switch(cellValue) {
          case 1:
            cellValueStr = '及时送'
          break;
          case 2:
            cellValueStr = '预约送'
          break;
          case 3:
            cellValueStr = '自提'
          break;
          case 5:
            cellValueStr = '快递配送'
          break;
          default:
            cellValueStr = '--'
          break;
        }
        return cellValueStr
      },
      /**
       * [emptyValFormater 空格式化]
       * @param  {[type]} row       [description]
       * @param  {[type]} column    [description]
       * @param  {[type]} cellValue [description]
       * @return {[type]}           [description]
       */
      emptyValFormater(row, column, cellValue) {
        return cellValue && cellValue != "" ? cellValue : '--'
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
    
  }
</script>




